@keyframes pulseExpand {
  0% {
    opacity: 0.7;
    transform: scale(1);
  }
  50% {
    opacity: 0.25;
    transform: scale(1.08);
  }
  100% {
    opacity: 0;
    transform: scale(1.18);
  }
}

.pulse-ring {
  background: radial-gradient(
    circle at center,
    rgba(251, 191, 36, 0.2),
    transparent 80%
  );
  opacity: 0;
  transform: scale(1);
  transition: opacity 0.3s ease;
}

.alert-high .pulse-ring {
