/* === Crowned X center glow (always-on pulse + brighter on hover) === */
@keyframes glowPulse {
  0%, 100% { transform: scale(1); opacity: .35; filter: blur(14px); }
  50%      { transform: scale(1.06); opacity: .48; filter: blur(16px); }
}
.glow-base {
  position: absolute;
  width: 56%;
  aspect-ratio: 1 / 1;
  border-radius: 9999px;
  background:
    radial-gradient(closest-side, rgba(251,191,36,.65), rgba(251,191,36,0) 70%),
    radial-gradient(closest-side, rgba(147,197,253,.50), rgba(147,197,253,0) 80%);
  mix-blend-mode: screen;
  filter: blur(14px);
  opacity: .38;
  transition: transform .6s ease, opacity .35s ease, filter .35s ease;
  animation: glowPulse 6s ease-in-out infinite;
}
.glow-ring {
  position: absolute;
  width: 64%;
  aspect-ratio: 1 / 1;
  border-radius: 9999px;
  border: 2px solid rgba(251,191,36,.35); /* amber-400 */
  box-shadow:
    0 0 24px rgba(251,191,36,.35),
    inset 0 0 16px rgba(59,130,246,.25);
  opacity: .4;
  transition: transform .6s ease, opacity .35s ease;
}
.group:hover .glow-base {
  opacity: .85;
  filter: blur(18px);
  transform: scale(1.15);
}
.group:hover .glow-ring {
  opacity: .9;
  transform: scale(1.08);
}

/* Optional: small brand mark glow (e.g., top-right logos) */
.brand-glow { position: relative; }
.brand-glow::before {
  content: "";
  position: absolute;
  inset: -12%;
  border-radius: 9999px;
  background:
    radial-gradient(closest-side, rgba(251,191,36,.45), rgba(251,191,36,0) 70%),
    radial-gradient(closest-side, rgba(147,197,253,.35), rgba(147,197,253,0) 80%);
  filter: blur(10px);
  opacity: 0;
  transition: opacity .25s ease, transform .25s ease, filter .25s ease;
  z-index: -1;
}
.brand-glow:hover::before {
  opacity: .9;
  filter: blur(14px);
  transform: scale(1.06);
}
/* === Peg (wedge) hover glow — subtle and classy ========================== */
.wedge {
  transform-box: fill-box;
  transform-origin: center;
  transition: filter .2s ease, opacity .2s ease;
}

.wedge:hover {
  /* soft white outer glow */
  filter: drop-shadow(0 2px 10px rgba(255,255,255,0.55))
          drop-shadow(0 6px 18px rgba(0,0,0,0.12));
}

/* faint edge light that appears only on hover */
.wedge-hover-rim {
  opacity: 0;
  transition: opacity .2s ease;
}
.wedge:hover .wedge-hover-rim {
  opacity: 1;
}

/* Reduced motion respect */
@media (prefers-reduced-motion: reduce) {
  .wedge, .wedge-hover-rim { transition: none !important; }
}
/* === Center logo sunshine glow + breathing =============================== */
.center-logo-wrap {
  position: relative;
  display: inline-block;
}

/* Ambient golden glow behind the logo */
.center-logo-wrap::before {
  content: "";
  position: absolute;
  inset: -44px; /* glow size around the logo */
  border-radius: 9999px;
  background:
    radial-gradient(closest-side, rgba(245, 158, 11, 0.28), rgba(245, 158, 11, 0.10) 55%, transparent 72%),
    radial-gradient(closest-side, rgba(251, 191, 36, 0.18), transparent 65%);
  filter: blur(6px);
  opacity: 0.9;
  z-index: 0; /* behind the logo image */
  animation: sunPulse 6s ease-in-out infinite;
  pointer-events: none;
}

/* Logo image */
.center-logo {
  position: relative;
  z-index: 1; /* above the glow */
  transition: transform .35s ease, filter .35s ease, opacity .35s ease;
  will-change: transform, filter;
}

/* “Breath” on hover */
.center-logo-wrap:hover .center-logo {
  transform: scale(1.06); /* gentle expand */
  filter:
    drop-shadow(0 4px 16px rgba(251, 191, 36, 0.35))   /* warm gold glow */
    drop-shadow(0 10px 26px rgba(0, 0, 0, 0.15));      /* soft depth */
}

/* Glow intensifies a touch on hover */
.center-logo-wrap:hover::before {
  opacity: 1;
  animation-duration: 5s;
}

/* Subtle pulsing of the glow */
@keyframes sunPulse {
  0%, 100% { transform: scale(1);    opacity: 0.8; }
  50%      { transform: scale(1.08); opacity: 1;   }
}

/* Motion accessibility */
@media (prefers-reduced-motion: reduce) {
  .center-logo-wrap::before {
    animation: none !important;
  }
  .center-logo,
  .center-logo-wrap:hover .center-logo {
    transition: none !important;
    transform: none !important;
    filter: none !important;
  }
}
